    if (username && password) {
      authService
        .login(username, password)
        .then(user => {
          localStorage.setItem("user", JSON.stringify(user));

          if (user.authentication) {
            this.props.history.push("/admin/dashboard");
          } else {
            this.setState({ loginError: "true" });
          }
        })
        .catch(error => {
          this.setState({ loginError: "true" });
        });
    }
